/**
 * ChocolateChip-UI Widget - Multi-Select List.
 */
$.extend({
  /**
   * Setup a multi-select list:
   */
  MultiSelectList: options => {
    if (!options || !options.element) return;
    let settings = {
      element: undefined,
      selected: [],
      name: $.uuid() + '[]',
      callback: $.noop,
      model: undefined
    };
    let __selection = [];
    $.extend(settings, options);
    let selections = settings.selected;
    const name = settings.name;
    const list = $(settings.element);
    list.addClass('multi-select-list');
    list.find('li').forEach(function(ctx, idx) {
      let value = ctx.getAttribute("data-select") !== null ? ctx.getAttribute("data-select") : "";
      selections.forEach(function(item) {
        if (item.index === idx) {
          __selection.push({
            index: idx,
            value: value
          });
        }
      });

      ctx.setAttribute('role', 'checkbox');
      $(ctx).removeClass('selected').find('input').removeAttr('checked');
      $(ctx).prepend(`<aside>
        <span class="multi-selection-indicator">
          <svg width="30px" height="30px" viewBox="0 0 30 30" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
            <g id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd"><g id="multi-select-icon" stroke="#979797">
              <g id="multi-select-circle-+-multi-select-checkmark" transform="translate(2.000000, 2.000000)">
                <circle id="multi-select-circle" cx="13" cy="13" r="13"></circle>
                <path d="M4.71521456,15.9877529 L13.0000002,20.7028494 L19.977049,5.70284941" id="multi-select-checkmark"></path>
                </g>
              </g>
            </g>
          </svg>
        </span>
      </aside>`);
      $(ctx).append(`<input type="checkbox" name="${ name }" value="${ value }">`);
      if (selections.length) {
        selections.forEach(function(sel) {
          if (sel === idx) {
            ctx.setAttribute('aria-checked', 'true');
            ctx.classList.add('selected');
            $(ctx).find('input').prop('checked', true).attr('value', value);
            __selection.push({
              index: sel,
              value: value
            });
          }
        });

      }
    });

    list.on('tap', 'li', function() {
      const item = $(this);
      if (item.hasClass('selected')) {
        item.removeClass('selected').removeAttr('aria-checked');
        item.find('input').removeProp('checked');
        let dataObj = {
          index: item.index(),
          value: item.attr('data-select')
        }
        let pos = undefined;
        __selection.forEach(function(item, idx) {
          if (item.index === dataObj.index && item.value === dataObj.value) {
            pos = idx;
          }
        });
        __selection.splice(pos, 1);

        settings.callback.apply(this, arguments);
      } else {
        __selection.push({
          index: item.index(),
          value: item.attr('data-select')
        });
        __selection.unique();
        item.addClass('selected');
        item.attr('aria-checked', true);
        item.find('input').prop('checked', true);

        settings.callback.apply(this, arguments);
      }

    });

    return {
      val: () => __selection,
      getSelection: () => __selection
    };
  }
});

/**
 * ChocolateChip-UI Widget - Adjust Navbar for iOS.
 */
$(() => {

  /**
   * Method to center H1 in Navbar.
   * Check on widths of siblings:
   */
  $.extend({

    /**
     * Track whether to add a global nav for styling navbars.
     */
    globalNav: false,

    AdjustNavbarLayout: function(screen) {
      if (!$('link[href*=ios]')[0]) return;
      if (document.body && document.body.hasAttribute('nocenterheader')) {
        return;
      }
      screen = $(screen);
      if (!screen[0]);
      const h1 = screen.find('h1');
      if (h1.hasClass('do-not-adjust')) return;
      const siblings = h1.siblings();
      let whichSide = undefined;
      let oppositeSide = undefined;
      const rtl = $('html').attr('dir') === 'rtl';
      let amount = 0;
      let hidden = false;
      let visibleSibling = undefined;

      const calculateLongest = function calculateLongest(a, b) {
        let widthA = a[0].clientWidth;
        let widthB = b[0].clientWidth;
        if (!widthA) {
          widthA = 0;
          whichSide = 'margin-right';
          oppositeSide = 'margin-left';
          if (rtl) {
            whichSide = 'margin-left';
            oppositeSide = 'margin-right';
          }
        }
        if (!widthB) {
          widthB = 0;
          whichSide = 'margin-left';
          oppositeSide = 'margin-right';
          if (rtl) {
            whichSide = 'margin-right';
            oppositeSide = 'margin-left';
          }
        }
        if (widthB > widthA) {
          whichSide = 'margin-left';
          oppositeSide = 'margin-right';
          if (rtl) {
            whichSide = 'margin-right';
            oppositeSide = 'margin-left';
          }
          amount = widthB - widthA;
        } else if (widthA > widthB) {
          whichSide = 'margin-right';
          oppositeSide = 'margin-left';
          if (rtl) {
            whichSide = 'margin-left';
            oppositeSide = 'margin-right';
          }
          amount = widthA - widthB;
        } else {
          amount = 0;
        }
      };

      function handleOneSibling(sib) {
        const sibling = sib || h1.siblings();
        amount = sibling[0].clientWidth;
        if (sibling.is(':first-child')) {
          whichSide = 'margin-right';
          oppositeSide = 'margin-left';
          if (rtl) {
            whichSide = 'margin-left';
            oppositeSide = 'margin-right';
          }
        } else if (sibling.is(':last-child')) {
          whichSide = 'margin-left';
          oppositeSide = 'margin-right';
          if (rtl) {
            whichSide = 'margin-right';
            oppositeSide = 'margin-left';
          }
        }
      }

      /**
       * If one sibling:
       */
      if (siblings.length === 1) {
        handleOneSibling();

        /**
         * If two siblings:
         */
      } else if (siblings.length === 2) {
          siblings.forEach(function (item) {
            if ($(item).css('display') === 'none') {
              hidden = true;
            } else {
              visibleSibling = $(item);
            }
          });
          if (hidden) {
            handleOneSibling(visibleSibling);
          } else {
            calculateLongest(siblings.eq(0), siblings.eq(1));
          }

          /**
           * H1 is alone:
           */
      } else {
        whichSide = 'margin-left';
        oppositeSide = 'margin-right';
        amount = 0;
      }
      let props = {};
      props[whichSide] = amount;
      props[oppositeSide] = 0;
      let sibwidth = 0;
      if (siblings.size()) {
        siblings.forEach(function (item) {
          sibwidth += $(item)[0].clientWidth;
        });
      }
      let headerWidth = screen.find('nav').width() / 2;
      if (sibwidth + 20 > headerWidth) {
        h1.css({
          'margin-left': 0,
          'margin-right': 0
        });
      } else {
        h1.css(props);
      }
    }
  });
  setTimeout(function() {
    if ($.globalNav === true && !$('#globalNav')[0]) {
      $('body').prepend('<nav id="globalNav"></nav>')
    }
    $('screen').forEach(function(screen) {
      $.AdjustNavbarLayout(screen);
    });
  });
});

/**
 * ChocolateChip-UI Widget - Navigation Module.
 */

$(() => {
  /**
   * Private variable to track navigation state:
   */
  let isNavigating = false;
  /**
   * get screen by id:
   */
  const getScreen = screen => $('#' + screen);

  /**
   * Handle state of screens:
   */
  const makeScreenCurrent = screen => {
    screen = $(screen);
    screen.addClass('current');
    screen.removeClass('previous');
    screen.removeClass('next');
  };
  const makeScreenPrevious = screen => {
    screen = $(screen);
    screen.removeClass('current');
    screen.removeClass('next');
    screen.addClass('previous');
  };
  const makeScreenNext = screen => {
    screen = $(screen);
    screen.removeClass('current');
    screen.removeClass('previous');
    screen.addClass('next');
  };

  $.extend({

    /**
     * Navigate to Specific Article
     */
    GoToScreen: destination => {
      if ($.isNavigating) return;
      $.isNavigating = true;
      setTimeout(function () {
        $.isNavigating = false;
      }, 500);
      $.ChuiRoutes.push(destination);
      const currentScreen = $.screens.getCurrent();
      const destinationScreen = function () {
        let temp = undefined;
        const regex = /:/img;
        temp = regex.test(destination) ? destination.split(':')[0] : destination;
        return getScreen(temp);
      }();
      if (currentScreen[0]) currentScreen[0].scrollTop = 0;
      if (destinationScreen[0]) destinationScreen[0].scrollTop = 0;
      makeScreenPrevious(currentScreen);
      makeScreenCurrent(destinationScreen);
      $.Router.dispatch(destination);
    },

    /**
     * Navigate Back to Previous Article
     */
    GoBack: () => {
      if ($.isNavigating) return;
      $.isNavigating = true;
      const currentScreen = $.screens.getCurrent();
      $.ChuiRoutes.pop();
      const destination = $.ChuiRoutes[$.ChuiRoutes.length - 1];
      const destinationScreen = function () {
        let temp = undefined;
        const regex = /:/img;
        temp = regex.test(destination) ? destination.split(':')[0] : destination;
        return getScreen(temp);
      }();
      if (currentScreen[0]) currentScreen[0].scrollTop = 0;
      if (destinationScreen[0]) destinationScreen[0].scrollTop = 0;
      makeScreenNext(currentScreen);
      makeScreenCurrent(destinationScreen);
      $.Router.dispatch(destination);
      setTimeout(function () {
        $.isNavigating = false;
      }, 500);
    },

    isNavigating: false,

    /**
     * Navigate Back to Non-linear Article
     */
    GoBackToScreen: destination => {
      const position = $.ChuiRoutes.findIndex(function(dest) {
        return dest = destination
      });
      const destinationScreen = getScreen(destination);
      while ($.ChuiRoutes.length > position + 1) {
        let route = $.ChuiRoutes.pop();
        route = route.split(':')[0];
        let screen = getScreen(route);
        makeScreenNext(screen);
      }
      makeScreenCurrent(destinationScreen);
      $.Router.dispatch(destination);
    }
  });

  /**
   * Initialize Back Buttons:
   */
  $('body').on('tap', '.back', function () {
    if (this.hasAttribute('disabled')) return;
    $.GoBack();
  });

  /**
   * Handle navigation events:
   */
  const handleNavigationEvent = element => {
    element = $(element);
    if ($.isNavigating) return;
    if (!element.hazAttr('data-goto')[0]) return;
    if (element.closest('ul').is('.deletable')) return;
    let destination = element.attr('data-goto');
    if (!destination) return;
    element.addClass('selected');
    setTimeout(() => element.removeClass('selected'), 1000);
    /**
     * Handle navigation:
     */
    $.GoToScreen(destination);
  };

  $('body').on('tap', 'li', function () {
    handleNavigationEvent($(this));
  });
  
  $('body').on('doubletap', 'li', function () {
    if (!$.isNavigating) {
      handleNavigationEvent($(this));
    }
  });
});

/**
 * ChocolateChip-UI Widget - Paging.
 */
$.extend({
  /**
   * Setup a paging control:
   */
  Paging: options => {
    if (!options || !options.element) return;
    const screen = $(options.element);
    const pager = 
      '<div class="pager">\n\
        <button class="previous">\n\
          <svg width="24px" height="36px" viewBox="0 0 24 36" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"><g id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd"><g id="pagination-icons" stroke="#979797"><path d="M19.7729197,3 L4.25431067,17.8699971 L19.7729196,32.9558941" id="page-previous"></path></g></g></svg>\n\
        </button>\n\
        <button class="next">\n\
          <svg width="24px" height="36px" viewBox="0 0 24 36" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"><g id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd"><g id="pagination-icons" stroke="#979797"><path d="M4.08862955,3.06871359 L20.0261609,18.0528447 L4.08862956,32.9999994" id="page-next"></path></g></g></svg>\n\
        </button>\n\
      </div>';

    $(screen).find('nav').append(pager);

    const currentSection = $(screen).find('section');

    const articles = function() {
      return currentSection.find('article').length;
    };
    $.AdjustNavbarLayout(screen);

    /**
     * Handle paging forward:
     */
    const pageForward = pagerButton => {
      if (articles() < 2) return;
      pagerButton.prev().removeClass('selected');
      pagerButton.addClass('selected');
      let currentArticle = undefined;
      if (pagerButton[0] && pagerButton[0].classList.contains('disabled')) return;
      currentArticle = currentSection.find('article.current');
      if (currentArticle.index() === articles() - 1) {
        /**
         * Start again!
         */
        currentArticle.removeClass('current').addClass('next');
        currentArticle.siblings().removeClass('previous').addClass('next');
        currentSection.find('article').eq(0).addClass('current').removeClass('previous').removeClass('next');
      } else {
        currentArticle.removeClass('current').addClass('previous');
        currentArticle.next().removeClass('next').addClass('current');
      }
      setTimeout(function() {
        pagerButton.removeClass('selected');
      }, 250);
    };

    const pageBack = pagerButton => {
      if (articles() === 1) return;
      pagerButton.next().removeClass('selected');
      pagerButton.addClass('selected');
      let currentArticle = undefined;
      currentArticle = currentSection.find('article.current');

      if (currentArticle.index() === 0) {
        currentArticle.removeClass('current');
        currentArticle.siblings().eq(-1).addClass('current').removeClass('next');
        currentArticle.siblings().eq(-1).siblings().removeClass('next').addClass('previous');
      } else {
        currentArticle.removeClass('current').addClass('next');
        currentArticle.prev().removeClass('previous').addClass('current');
      }
      setTimeout(() => {
        pagerButton.removeClass('selected');
      }, 250);
    };

    $('.pager').on($.eventStart, 'button:last-of-type', () => {
      pageForward($(this));
    });
    $('.pager').on($.eventStart, 'button:first-of-type', () => {
      pageBack($(this));
    });

  }
});

/**
 * ChocolateChip-UI Widget - Popover.
 */
$.extend({
  /**
   * Setup a popover (dropdown menu):
   */
  Popover: options => {
    /**
      id: myUniqueID,
      title: 'Great',
      callback: myCallback,
    */
    options = options || {};
    let settings = {
      id: $.uuid(),
      callback: $.noop,
      title: '',
    };
    $.extend(settings, options);
    if (options && options.content) {
      settings.content = options.content;
    } else {
      settings.content = '';
    }
    const header = `<header><h1>${ settings.title }</h1></header>`;
    const popover = `<div class="popover" id="${ settings.id }">${ header }<section></section></div>`;
    const popoverID = '#' + settings.id;

    /**
     * Calculate position of popover relative to the button that opened it:
     */
    const __calcPopPos = element => {
      const offset = $(element).offset();
      let left = offset.left;
      let calcLeft = undefined;
      let calcTop = undefined;
      let popover = $(popoverID);
      const popoverOffset = popover.offset();
      calcLeft = popoverOffset.left;
      calcTop = offset.top + $(element)[0].clientHeight;
      if ((popover.width() + offset.left) > window.innerWidth) {
        popover.css({
          'left': ((window.innerWidth - popover.width()) - 20) + 'px',
          'top': (calcTop) + 25 + 'px'
        });
      } else {
        popover.css({
          'left': left + 'px',
          'top': (calcTop - 30) + 'px'
        });
      }
    };

    if ($('.mask')[0]) {
      $.ClosePopover();
      $('body').Unblock();
      return;
    }
    $.Block('.5');
    $('body').append(popover);
    if ($('body').hasClass('themeIsAndroid')) {
      setTimeout(() => {
        $(popoverID).addClass('opened');
      }, 200);
    }
    $(popoverID).data('triggerEl', settings.trigger);
    $(popoverID).find('section').append(settings.content);
    settings.callback.call(settings.callback, settings.trigger);
    __calcPopPos(settings.trigger);

  },

  AlignPopover: () => {
    let popover = $('.popover');
    if (!popover.length) return;
    const triggerID = popover.data('triggerEl');
    const offset = $(triggerID).offset();
    const left = offset.left;
    if (($(popover).width() + offset.left) > window.innerWidth) {
      popover.css({
        'left': ((window.innerWidth - $(popover).width()) - 20) + 'px'
      });
    } else {
      popover.css({
        'left': left + 'px'
      });
    }
  },

  ClosePopover: () => {
    $.Unblock();
    $('.popover').css({transform: 'scaleY(0)'});
    setTimeout(() => {
      $('.popover').off();
      $('.popover').remove();
    }, 100);
  }
});

$(() => {
  /**
   * Reposition popovers on window resize:
   */
  window.onresize = () => {
    $.AlignPopover();
  };
  // const events = "$.eventStart singletap $.eventEnd";
  $('body').on($.eventStart, '.mask', e => {
    // if ($('.popover')[0]) {
    //   if (e && e.nodeType === 1) return;
    // } else {
      // alert('closing popover')
      $.ClosePopover();
    // }
  });
});

/**
 * ChocolateChip-UI Widget - Popup.
 */
$.extend({
  /**
   * Setup  a popup dialog:
   */
  Popup: function(options) {
    /**
    options {
      id: 'alertID',
      title: 'Alert',
      message: 'This is a message from me to you.',
      cancelButton: 'Cancel',
      continueButton: 'Go Ahead',
      width: '100px',
      callback: function() { // do something },
      empty: true
    }
    */
    if (!options) return;
    let settings = {};
    settings.id = $.uuid();
    settings.content = true;
    $.extend(settings, options);
    let width = '';
    if (settings.width) {
      width = `style="width:${settings.width}px;"`;
    }

    const id = settings.id;
    const title = settings.title ? `<header><h1>${settings.title}</h1></header>` : '';
    const message = settings.message ? `<p role="note">${options.message}</p>` : '';
    const cancelButton = options.cancelButton ? `<button class="cancel" role="button">${settings.cancelButton}</button>` : '';
    const continueButton = settings.continueButton ? `<button class="continue" role="button">${settings.continueButton}</button>` : '';
    const callback = settings.callback || $.noop;
    let popup = undefined;
    if (settings.empty) {
      popup = `<div id="${id}" class="popup"><div ${width} class="dialog" role="alertdialog"></div></div>`;
    } else {
      popup = `<div class="popup" id="${id}"><div class="dialog" role="alertdialog"><div class="panel">${title}${message}</div><footer>${cancelButton}${continueButton}</footer></div></div>`;
    }
    $('#' + id).remove();
    $('body').append(popup);
    if (settings.empty) {
      $('.dialog').css({
        "display": "-webkit-flex",
        "-webkit-justify-content": "center", 
        "-webkit-align-items": "center", 
        "display": "flex",
        "justify-content": "center", 
        "align-items": "center"
      })
    }
    if (callback && continueButton) {
      $('#' + id).find('.continue').on('tap', function () {
        const $this = $(this);
        if ($.isAndroid || $.isChrome) {
          $this.addClass('selected');
          setTimeout(function () {
            $this.removeClass('selected');
            $('.popup').ClosePopup();
            callback.call(callback);
          }, 300);
        } else {
          $('#' + id).ClosePopup();
          callback.call(callback);
        }
      });
      $('#' + id).find('.cancel').on('tap', function () {
        const $this = $(this);
        if ($.isAndroid || $.isChrome) {
          $this.addClass('selected');
          setTimeout(function () {
            $this.removeClass('selected');
            $('#' + id).ClosePopup();
          }, 300);
        } else {
          $('#' + id).ClosePopup();
        }
      });
    }

    $('.popup').on('tap', function (e) {
      $(this).removeClass('opened')
    });
  },

});
$.fn.extend({
  /**
   * Show Popup:
   */
  ShowPopup: function ShowPopup() {
    const self = $(this);
    self.removeClass('forPopup')
    self.addClass('opened');
    setTimeout(function() {
      self.find('.popup').addClass('opened')
    }, 0)
  },

  /**
   * Close Popup:
   */
  ClosePopup: function ClosePopup() {
    const self = $(this);
    self.removeClass('opened');
    self.find('.popup').removeClass('opened')
  }
});

/**
 * ChocolateChip-UI Widget - Range Input.
 */
$.fn.extend({
  /**
   * Setup a range input:
   */
  Range: function() {
    if (this[0].nodeName !== 'INPUT') return;
    let input = $(this);
    let newPlace = undefined;
    const width = input.width();
    const newPoint = (input.val() - input.attr("min")) / (input.attr("max") - input.attr("min"));
    let offset = -1.3;
    if (newPoint < 0) {
      newPlace = 0;
    } else if (newPoint > 1) {
      newPlace = width;
    } else {
      newPlace = width * newPoint + offset;
      offset -= newPoint;
    }
    if ($.theme === 'android') {
      input.css({
        'background-size': Math.round(newPlace) + 'px 3px, 100% 3px'
      });
    } else {
      input.css({
        'background-size': Math.round(newPlace) + 'px 10px'
      });
    }
  }
});
$(() => {
  $('input[type=range]').forEach(ctx => {
    $(ctx).Range();
  });
  $('body').on('input', 'input[type=range]', function() {
    $(this).Range();
  });
});

/**
 * ChocolateChip-UI Widget - Router.
 */
$.extend({
  /**
   * Define Router:
   */
  ChuiRoutes: [],

  Router: () => {

    return {
      addRoute: options => {
        if ($.type(options) === 'array') {
          options.forEach(item => $.on(item.route, item.callback));
        }
      },

      getFullRoute: () => $.ChuiRoutes.join('/'),

      getCurrentLoc: () => {
        const temp = this.getFullRoute().split('/');
        return temp[temp.length - 1];
      },

      dispatch: route => {
        let temp = undefined;
        let id = undefined;
        if (route.match(/\:/)) {
          temp = route.split(':');
          id = temp[1];
          route = temp[0];
        }
        $.send(route);
      }
    };
  }
});

$.extend($.Router, {
  dispatch: route => {
    if (!route) return;
    let temp = undefined;
    let id = undefined;
    if (route.match(/\:/)) {
      temp = route.split(':');
      id = temp[1];
      route = temp[0];
    }
    $.send(route, id)
  }
});

$.extend($.ChuiRoutes, {
  getFullRoute: function getFullRoute() {
    return this.join('/');
  }
});

$(() => {
  /**
   * Set up initial route:
   */
  if ($('screen').size() && !$.ChuiRoutes.length) {
    $.ChuiRoutes.push($('screen')[0].id);
  }
});

